@import "../variables";

.HorizontalSlide {
  align-items: center;
  display: flex;

  .swiper {
    margin-left: initial;
    margin-right: initial;
    width: 100%;

    .swiper-wrapper {
      padding: $spacing 0 $padding;
      width: 100%;
      z-index: $z-base;

      .swiper-slide {
        top: 0;
        transition: top 0.1s ease-out;
        width: 200px;

        @media only screen and (min-width: $br-phone) {
          width: 30%;
        }

        @media only screen and (min-width: $br-phablet) {
          width: 25%;
        }

        @media only screen and (min-width: $br-tablet) {
          width: 20%;
        }

        @media only screen and (min-width: $br-laptop) {
          width: 15%;
        }

        &:hover {
          top: -0.125em;

          img {
            box-shadow: 0 4px 10px rgb($dark-background, 1);
          }
        }

        .Cover {
          box-shadow: 0 0 6px rgb($dark-background, 0.16);
          transition: box-shadow 0.1s ease-out;
          width: 100%;
        }

        h6 {
          line-height: 1;
          margin: 0;
        }
      }
    }
  }

  &.small {
    .swiper .swiper-wrapper .swiper-slide {
      width: 100px;

      @media only screen and (min-width: $br-phone) {
        width: 15%;
      }

      @media only screen and (min-width: $br-phablet) {
        width: 12.5%;
      }

      @media only screen and (min-width: $br-tablet) {
        width: 10%;
      }

      @media only screen and (min-width: $br-laptop) {
        width: 7.5%;
      }
    }
  }
}
